.tooltip
{
    visibility: hidden;
  width: 120px;
  background-color: rgb(152, 163, 224);
  color: black;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  
  /* Position the tooltip */
  position: absolute;
  z-index: 1;
  top: 100%;
  left: 50%;
  margin-left: -60px;
}
.html:hover .tooltip {
    visibility: visible;
}

