/*styles for project page*/

.content-box
{
    position: relative;
    width: 20vw;
    height: 69vh;
    background-color: #bec4ec;
    
    border-radius: 2%;
    box-shadow: 5px 5px 5px lightgray;
    
}


.content-box:hover
{
    background-color: #d9deff;

}

