.contactButton
{
    
    letter-spacing: 1px;
    background-color: #7179a7;
    padding: 12px 40px;
    font-size: .9rem;
    color: white;
    border-radius: 40px;
    width: 140px;
    border: none;
    cursor: pointer;
    transition: all .3s ease-in;
    font-style: normal;
    
}
.contactButton a
{
    text-decoration: none;
    color: white;
}
.contactButton:hover
{
