.nav
{
    background-color: #7179a7;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2rem;
    padding: 0 1rem;
}

nav ul
{
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
}

.site-title
{
    font-size: 2rem;
    text-decoration: none;
}

.nav a
{
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
}

.nav li.active 
{
    background-color: #4f5575;
}

.nav li:hover
{
    background-color: rgb(152, 163, 224);
}

